@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
:root{
  --primary_color:#0d0d26;
  --white:#fff;
  --black:#000;
  --font_weight_regular:400;
  --font_weight_medium:500;
  --font_weight_semibold:600;
  --font_weight_bold:700;
}
*{
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;

}
html {
  scroll-behavior: smooth;
}
body{
  box-sizing: border-box;
}


/* navbar */

.navbar{
  z-index: 100;
  position: fixed;
  width: 100%;
  padding: 1.5% 2%;
  transition: .5s;
}
.navbar.sticky{
  background-color: #0d0d26;
  background-color: var(--primary_color);
  color: #0d0d26;
  color: var(--primary_color);
}
.navbar-nav{
  margin: 0 20%;
}
.navbar-brand{
  color: #fff;
  color: var(--white);
  font-size: 2rem;
  font-family: 'Lobster', cursive;
}
.navbar-brand:hover{
  color: #fff;
  color: var(--white);
}
.nav-item{
  display: block;
  margin-right: 4%;
}
.nav-link{
  color: #fff;
  color: var(--white);
  font-size: 1.1rem;
  font-weight: 400;
  font-weight: var(--font_weight_regular);
  transition: .2s;
  position: relative;
  border-bottom: 2px solid #0d0d26;
  border-bottom: 2px solid var(--primary_color);
  transition: .5s;
}

.nav-link:hover{
  color: #fff;
  color: var(--white);
  border-bottom: 2px solid #fff;
  border-bottom: 2px solid var(--white);
}

.active{
  border-bottom: 2px solid #0d0d26;
  border-bottom: 2px solid var(--primary_color);
}
.form-inline .form-control{
  outline: none !important;
  box-shadow: none;
}
.account{
  font-size: 2rem;
  cursor: pointer;
}
.btn:focus,.btn:active {
  outline: none !important;
  box-shadow: none;
}

/* Banner */

.container-home{
  height: 100vh;
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}
.container-home .left{
  width: 75%;
  height: 100vh;
  background-color:#0d0d26;
  background-color:var(--primary_color);
  position: relative;
}
.container-home .right{
  width: 25%;
  height: 100vh;
  background-color: white;
  position: relative;
}

.container-home .container-fluid{
  position: absolute;
  color: #fff;
  color: var(--white);
  margin: 10% auto;
}
.container-home .container-fluid .start{
  width: 75%;
  padding-top: 9%;
  padding-left: 10%;
}
.container-home .container-fluid .end{
  width: 25%;
  padding-top: 4%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.container-home .container-fluid .end img{
  display: block;
  /* margin: 0 auto; */
}
.container-home .container-fluid .start h3,h4,p{
    width: 100%;
    padding-top: 2%;
}
.container-home .container-fluid .start h3{
  letter-spacing: 2px; 
  font-size: 2.5rem;
  font-weight: 700;
  font-weight: var(--font_weight_bold);
}
.container-home .container-fluid .start .btn{
  width: 12rem;
  background-color: #fff;
  background-color: var(--white);
  font-size: 1.1rem;
  font-weight: 500;
  font-weight: var(--font_weight_medium);
  margin-top: 2%;
  padding-left: 3%;
  border-radius: 30px;
}
.container-home .container-fluid .start .btn .arrow{
  background-color: #0d0d26;
  background-color: var(--primary_color);
  color: #fff;
  color: var(--white);
  border-radius: 50%;
  height: 2rem;
  width: 2rem;
  margin-left: 15%;
}

/* backtext */
.backtext h1{
  opacity: 0.07;
  font-size: 15rem;
  font-weight: 700;
  font-weight: var(--font_weight_bold);
  z-index: 1;
  margin-left: -2%;
  position: absolute;
}

/* New Arrive */

.new-arrive{
  height: 100%;
  width: 100%;
}
.heading{
  position: relative;
  width: 100%;
  padding: 15% 0 7% 3%;
}
.heading h4{
  line-height: 0rem;
  font-size: 2rem;
  /* letter-spacing: 2px; */
}
.heading p{
  line-height: 0.6rem;
  font-size: 1.1rem;
}
.categories{
  background-color: #0d0d26;
  background-color: var(--primary_color);
  height: 100%;
}
.categories .container{
    padding-bottom: 10%;
}
.categories .card{
  height: 8rem;
  border: none;
  margin-bottom: 2%;
  border-radius: 10px;
  padding: 3% 10%;
}
.categories .card i{
  font-size: 3rem;
  padding: 1% 0;
}
.categories .card:hover{
  cursor: pointer;
}

.new-arrive .card{
  height: 100%;
  border: none;
  box-shadow: 2px 2px 10px 2px rgba(0, 0, 0, 0.086);
  border-radius: 5%;
  justify-content: center;
  display: flex;
  align-items: center;
}
.new-arrive .card .card-title{
  line-height: 0;
  font-size: 1.1rem;
}
.new-arrive .card .card-text{
  line-height: 1.2rem;
  font-size: 0.9rem;
  height: 3rem;
}
.new-arrive .card .card-price{
  color: #0d0d26;
  color: var(--primary_color);
  font-weight: 700;
  font-weight: var(--font_weight_bold);
  font-size: 1.1rem;
  line-height: 1rem;
}
.new-arrive .card img{
  width: 70%;
}
.new-arrive .card .btn{
  width: 10rem;
  background-color: #fff;
  background-color: var(--white);
  color:#000;
  color:var(--black);
  border: 3px solid #0d0d26;
  border: 3px solid var(--primary_color);
  font-weight: 500;
  font-weight: var(--font_weight_medium);
  transition: .4s;
  z-index: 1;
  position: relative;
}
.new-arrive .card .btn::after{
  content: '';
  position: absolute;
  width: 0%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
  transition: .4s;
  background-color: #0d0d26;
  background-color: var(--primary_color);
}
.new-arrive .card .btn:hover::after{
  width: 100%;
}
.new-arrive .card .btn:hover{
  color: #fff;
  color: var(--white);
}

/* best offfers */
.best-offer .container-fluid .row {
  height: 20rem;
}
.best-offer .container-fluid img{
  height: 100%;
  width: 100%;
  left: 0;
  position: absolute;
  /* padding-bottom: 4%; */
  /* background-size: 100% 100%; */
}
.best-offer .container-fluid .overlay{
  height: 100%;
  width: 100%;
  position: absolute;
  top:0;
  left:0;
  display: flex;
  background-color: rgba(0,0,0,0.6);
  color: #fff;
  color: var(--white);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.best-offer .container-fluid .overlay h4{
    font-size: 1.6rem;
}
.best-offer .container-fluid .overlay p{
  width: 40%;
  font-size:1rem;
}

/* explore-top */


.explore-top .container img{
  height: 100%;
  padding: 10% 30% 0% 30%;
  background-size: 100% 100%;
  width: 100%;
  cursor: pointer;
}

/* Footer */

.footer{
  background-color: #0d0d26;
  background-color: var(--primary_color);
  margin-top: 8%;
  padding: 5% 5%;
}
.footer .container h4{
  color: #fff;
  color: var(--white);
  font-size: 2rem;
  font-family: 'Lobster', cursive;
  cursor: pointer;
}
.footer .container i{
  color: #fff;
  color: var(--white);
  font-size: 1.5rem;
  padding-left: 2.5%;
  cursor: pointer;
}
.footer .container p{
  color: #fff;
  color: var(--white);
  font-size: 1.4rem;
}
.footer .container input{
  border-left: none;
  border-right: none;
  border-top: none;
  border-bottom: 1px solid #fff;
  border-bottom: 1px solid var(--white);
  padding: 2% 0;
  background-color: #0d0d26;
  background-color: var(--primary_color);
  width: 20rem;
  color: #fff;
  color: var(--white);
}
.footer .container input:focus{
  color: #fff;
  color: var(--white);
}
.footer .container .btn{
  border: 1.5px solid #fff;
  border: 1.5px solid var(--white);  
  margin-left: 20%;
  color: #fff;
  color: var(--white);
  z-index: 1;
  position: relative;
}

.owl-theme{
  border: none;
  outline: none;
}
.owl-item .item { margin-left: 1px !important; }
.owl-carousel .item .card{
  margin: 0;
  padding: 0;
  height: 22rem;
  width: 16rem;
}
.owl-carousel .owl-prev{
  /* height: 10rem;font-size: 10rem; */
  background-color: #000;
}

